import React from "react";
import { StaticQuery, graphql } from "gatsby";
import styled from "@emotion/styled";
import { Channel } from "./Channel";

const ChannelBar = styled.div`
  display: flex;
`;

export const Channels = () => (
  <StaticQuery
    query={graphql`
      query ChannelsQuery {
        allChannelsYaml(limit: 3) {
          edges {
            node {
              id
              title
              username
              url
              icon
            }
          }
        }
      }
    `}
    render={({ allChannelsYaml: { edges } }) => (
      <ChannelBar>
        {edges.map(({ node }) => (
          <Channel key={node.id} {...node} />
        ))}
      </ChannelBar>
    )}
  />
);
